.stepper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.step {
  text-align: center;
  opacity: 0.4;
  flex: 1;
}

.step p {
  margin-top: 10px;
  font-size: 12px;
  font-weight: 700;
}

.circle {
  width: 70px;
  height: 70px;
  background: #f2f2f2;
  border-radius: 50%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.active {
  opacity: 1;
}

.active .circle {
  background: #fff;
  box-shadow: 0 0 0 6px #f1e7ff;
}
