.card {
  background: #f2f2f2;
  padding: 24px;
  border-radius: 14px;
  margin-bottom: 24px;
  border: 1px solid #eee;
}

.cardTitle {
  font-weight: 700;
  margin-bottom: 18px;
}

.cardHeader {
  display: flex;
  justify-content: space-between;
}

.grid3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.input {
  height: 44px;
  border-radius: 10px;
  border: 1px solid #ddd;
  padding: 0 14px;
}

.textarea {
  width: 100%;
  min-height: 120px;
  border-radius: 10px;
  border: 1px solid #ddd;
  padding: 12px;
  margin-top: 16px;
}

.actions {
  display: flex;
  justify-content: space-between;
}

.btnPrimary {
  background: #7f00ff;
  color: #fff;
  border: none;
  padding: 12px 40px;
  border-radius: 25px;
}

.btnOutline {
  border: 2px solid #7f00ff;
  background: #fff;
  padding: 12px 40px;
  border-radius: 25px;
}
