.card {
  background: #fafafa;
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 24px;
  border: 1px solid #eee;
}

.input {
  width: 100%;
  height: 44px;
  border-radius: 8px;
  border: 1px solid #ddd;
  padding: 0 12px;
  margin-top: 12px;
}

.empty {
  color: #7f00ff;
}

.actions {
  display: flex;
  justify-content: space-between;
}

.primaryBtn {
  background: #7f00ff;
  color: #fff;
  border: none;
  padding: 12px 40px;
  border-radius: 24px;
}

.outlineBtn {
  border: 2px solid #7f00ff;
  background: #fff;
  padding: 12px 40px;
  border-radius: 24px;
}
